import React from 'react';
export default function CardProperty({
  text,
  isActive = false,
  currentValue: value,
  previousValue,
}: {
  text: string;
  isActive?: boolean;
  currentValue: number;
  previousValue?: number;
}) {
  const activeState = 'font-bold';
  const increasedValue = previousValue ? value - previousValue : 0;
  return (
    <div
      className={`flex justify-between items-center relative pl-2 ${
        isActive && activeState
      } rounded-lg`}
    >
      {/* red dot pulsing if new data  */}

      <span>{text}:</span>

      <div>
        {value.toLocaleString()}{' '}
        <span className="text-red-800  font-normal">
          {Math.floor(increasedValue) !== 0
            ? `(â–² ${Math.floor(increasedValue)})`
            : undefined}
        </span>
      </div>
    </div>
  );
}
