import Country from '../models/country';
import { CountryProps, HistoricalCountry } from '../interfaces';

const countriesUrl = 'https://corona.lmao.ninja/v2/countries';
export async function getCountriesJson(
  minCases: number,
  sortProperty: CountryProps
) {
  let countriesJson: Country[] = [];

  try {
    countriesJson = await await (await fetch(countriesUrl)).json();
    const countries = countriesJson
      .filter((c) => c.cases > minCases)
      .sort((a, b) => b[sortProperty] - a[sortProperty])
      .map((c) => Object.assign(new Country(), c));

    countries.forEach((c, i) => (c.ranking = i + 1));

    return countries;
  } catch (error) {
    console.log(error);
    return [];
  }
}

const countryUrl = (countryName: string) =>
  `https://corona.lmao.ninja/v2/historical/${countryName}?lastdays=95`;
export async function getCountryReport(countryName: string) {
  const countryJson = (await await (
    await fetch(countryUrl(countryName))
  ).json()) as HistoricalCountry;
  const timeline = countryJson.timeline;
  const cases = timeline.cases as { [key: string]: number };
  const deaths = timeline.deaths as { [key: string]: number };
  const days = Object.keys(timeline.cases)
    .map((k, i, arr) => {
      let items = k.split('/');
      if (items[0].length === 1) items[0] = '0' + items[0];
      if (items[1].length === 1) items[1] = '0' + items[1];

      const date = getMonthAbb(items[0]) + ' ' + items[1];

      const acumulatedCases: number = cases[k];
      const acumulatedDeaths = deaths[k];

      let todayCases =
        i === 0 ? acumulatedCases : acumulatedCases - cases[arr[i - 1]];
      let todayDeaths =
        i === 0 ? acumulatedDeaths : acumulatedDeaths - deaths[arr[i - 1]];

      return {
        date: date,
        cases: acumulatedCases,
        todayCases,
        deaths: acumulatedDeaths,
        todayDeaths,
      };
    })
    .filter((d) => d.cases !== 0)
    //WTF this is sorted by WHAT?!
    .sort();

  return days;
}

const getMonthAbb = (x: string) => {
  const months = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dez',
  ];
  const monthNumber = Number(x) - 1;
  return months[monthNumber];
};
